import React, { useState, useEffect } from 'react';
import { sqlFromServer } from '../api';
import { useParams, useHistory } from 'react-router-dom';
const Result = () => {

  const [sqlResult, setSqlResult] = useState([
      {
        nametv: 'Даного коду товару не знайдено',
        codetv: '1'
      }
    ]);
  const params = useParams();
  const inputval = params.inputval;
  const typeval = params.typeval;
  console.log(inputval);
  console.log(typeval);

  useEffect(() => {

    async function getData() {
      await sqlFromServer(inputval, typeval)
      .then(res => setSqlResult(res));
    };
    getData();

  },[inputval, typeval]);


  const history = useHistory();
  const onClickHandler = (codetv) => {
    if(codetv !== '1') {
      history.push(`/remainder/${codetv}`);
    }
  };


  return (
    <div>
      <table className="table table-dark">
        <thead>
          <tr>
            <th scope="col">Назва товару</th>
            <th scope="col">Артикуль</th>
          </tr>
        </thead>
        <tbody>
        {sqlResult.map(result => (
          <tr
          className="result__row"
          key={result.codetv}
          onClick={onClickHandler.bind(this, result.codetv)}
          >
            <th scope="row">{sqlResult.indexOf(result) + 1}</th>
            <td>{result.nametv}</td>
            <td>{result.article}</td>
          </tr>
          ))
          }
        </tbody>
      </table>
    </div>
  );
}

export default Result;
