import React, { useState, useEffect } from 'react';
import { sqlFromServer, sqlFromServerCodetv1, sqlFromServerCodetv2 } from '../api';
import { useParams } from 'react-router-dom';

const Remainder = () => {

  const [sqlResult, setSqlResult] = useState(
    {
      nametv: 'Даного коду товару не знайдено',
      codetv: '1'
    }
  );

  const [priceSql, setPriceSql] = useState([
    {

    }
  ]);

  const [remaindersSql, setRemaindersSql] = useState([
    {
      
    }
  ]);
  const params = useParams();
  const codetv = params.codetv;

  useEffect(() => {
    async function getData() {
      await sqlFromServer(codetv, '0')
      .then(res => setSqlResult(...res));
    };
    async function getPrice() {
      await sqlFromServerCodetv2(codetv)
      .then(res => setPriceSql(res));
    };
    async function getRemainders() {
      await sqlFromServerCodetv1(codetv)
      .then(res => setRemaindersSql(res));
    };
    getData();
    getPrice();
    getRemainders();
  },[codetv]);

  return (
    <div className="remainder">
      <div className="remainder__title" key={sqlResult.article}>
        <h5 className="remainder__name">{sqlResult.nametv}</h5>
        <p className="card__article">Артикул: {sqlResult.article}</p>
        <p className="card__article">Код: {sqlResult.codetv}</p>
      </div>

      <div className="remainder__price">
        <table className="table table-dark">
          <thead>
            <tr>
              <th scope="col">Ознака ціни</th>
              <th scope="col">КвУ</th>
              <th scope="col">Ціна</th>
            </tr>
          </thead>
          <tbody>
          {priceSql.map(price => (
            <tr
            className="result__row"
            key={price.ozn}
            >
              <td>{price.ozn}</td>
              <td>{price.kvu}</td>
              <td>{price.cenar1} грн</td>
            </tr>
            ))
            }
          </tbody>
        </table>
      </div>

      <div className="remainder__remainders">
        <table className="table table-dark">
          <thead>
            <tr>
              <th scope="col">Назва підприємства</th>
              <th scope="col">Кількість</th>
            </tr>
          </thead>
          <tbody>
          {remaindersSql.map(item => (
            <tr
            className="result__row"
            key={item.viddil}
            >
              <td>{item.viddil}</td>
              <td>{item.kol}</td>
            </tr>
            ))
            }
          </tbody>
        </table>
      </div>
      <img src="./img/icons/Totalcmd.ico" alt=""/>
    </div>
  );
}

export default Remainder;
