import React from 'react';
import { Route, Switch } from 'react-router-dom';

import './App.scss';
import Header from './components/Header/Header';
import HomePage from './components/HomePage/HomePage';
import BarCode from './components/BarCode/BarCode';
import Result from './components/Result/Result';
import Remainder from './components/Remainder/Remainder';


const App = () => (
  <div className="App">
    <Header />
    <Switch>
      <Route path="/" exact component={HomePage} />
      <Route path="/barcode" exact component={BarCode} />
      <Route path="/result/:inputval/:typeval" exact component={Result} />
      <Route path="/remainder/:codetv" exact component={Remainder} />
    </Switch>
  </div>
);

export default App;
