const API_URL = 'https://afternoon-atoll-51871.herokuapp.com/'

export const sqlFromServer = async (inputval, typeval) => (
  fetch(`${API_URL}/sql/${inputval}/${typeval}`)
  .then(response => response.json())
);

export const sqlFromServerCodetv1 = async (codetv) => (
  fetch(`${API_URL}/sql2/${codetv}`)
  .then(response => response.json())
);

export const sqlFromServerCodetv2 = async (codetv) => (
  fetch(`${API_URL}/sql3/${codetv}`)
  .then(response => response.json())
);

